import React from "react";
import { Box } from "@mui/material";

import Loader from "../../common/loader/Loader";
import { usePpiBankTransfer } from "./BankTransferPPIProvider";

import PpiStatusPage from "./PpiStatusPage";
import PpiSendMoney from "./PpiSendMoney";
import PpiAddBeneficiary from "./PpiAddBeneficiary";
import PpiAddCustomer from "./PpiAddCustomer";

const PpiIndex = () => {
  const { globalLoader, currentPage } = usePpiBankTransfer();

  return (
    <Box sx={{ width: "100%", p: 2 }}>
      {globalLoader && <Loader />}

      {currentPage === "ppi-status-page" && <PpiStatusPage />}
      {currentPage === "ppi-add-customer" && <PpiAddCustomer />}
      {currentPage === "ppi-add-beneficiary" && <PpiAddBeneficiary />}
      {currentPage === "ppi-send-money" && <PpiSendMoney />}
    </Box>
  );
};

export default PpiIndex;
