import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  TextField,
  Typography,
  Dialog,
  DialogContent,
} from "@mui/material";

import Autocomplete from "@mui/material/Autocomplete";
import { toast } from "react-toastify";

import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import { getPpiBankList, addPpiBeneficiary, checkVerifyRemitter } from "../../api/login/Login";
import PpiPageLayout from "./PpiPageLayout";

const PpiAddBeneficiary = () => {
  const { setCurrentPage, setBeneficiaryList, setGlobalLoader } = usePpiBankTransfer();

  const [bankList, setBankList] = useState([]);
  const [openOtpPopup, setOpenOtpPopup] = useState(false);
  const [otp, setOtp] = useState("");
  const [mobile, setMobile] = useState("");

  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  const [form, setForm] = useState({
    name: "",
    accountNumber: "",
    ifsc: "",
    bankId: "",
  });

  const fetchBankList = async () => {
    try {
      const res = await getPpiBankList();
      setBankList(res?.data?.data || []);
    } catch {
      toast.error("Bank List Load Failed");
    }
  };

  useEffect(() => {
    fetchBankList();
  }, []);

  const handleSubmit = async () => {
    if (!form.name || !form.accountNumber || !form.ifsc || !form.bankId) {
      toast.error("All fields are required");
      return;
    }

    const payload = {
      name: form.name,
      accountNumber: form.accountNumber,
      ifsc: form.ifsc,
      bankId: form.bankId,
      mobile,
    };

    setGlobalLoader(true);

    try {
      await addPpiBeneficiary(payload);

      const sendOtpRes = await checkVerifyRemitter({ mobile });
      toast.success("OTP Sent Successfully");
      setOpenOtpPopup(true);
    } catch (err) {
      toast.error("Failed to add beneficiary");
    }

    setGlobalLoader(false);
  };


  const handleVerifyOtp = async () => {
    if (!otp) return toast.error("Enter OTP");

    setGlobalLoader(true);

    try {
      const res = await checkVerifyRemitter({ mobile, otp });
      const responseBody = res?.data?.data;
      const statusCode = responseBody?.statuscode;

      if (statusCode === "TXN") {
        toast.success("Beneficiary Added Successfully!");
        setBeneficiaryList(responseBody?.data);
        setOpenOtpPopup(false);
        setCurrentPage("ppi-status");
      }

      else if (statusCode === "RNF") {
        toast.info("Remitter Not Found, Please Register First");
        setOpenOtpPopup(false);
        setCurrentPage("ppi-add-customer");
      }

      else if (statusCode === "IST") {
        toast.error("Session Expired");
        setOpenOtpPopup(false);
        setCurrentPage("ppi-status");
      }

      else if (statusCode === "KYC") {
        toast.error("Mobile not linked with Aadhaar. Use Bio-auth to complete KYC");
        setOpenOtpPopup(false);
      }

      else {
        toast.error(responseBody?.status || "Verification Failed");
      }

    } catch (err) {
      toast.error("Invalid OTP");
    }

    setGlobalLoader(false);
  };


  return (
    <PpiPageLayout title="Add Beneficiary">
      <Card
        sx={{
          p: { xs: 2, sm: 3 },
          borderRadius: "16px",
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          border: "1.5px solid var(--bs-primary2)",
          boxShadow: "none",
          maxWidth: "400px",
        }}
      >
        {/* Registered Mobile Display */}
        <TextField
          fullWidth
          label="Registered Mobile Number"
          value={mobile}
          disabled
          sx={{ mb: 2 }}
        />

        <TextField
          fullWidth
          label="Beneficiary Name"
          name="name"
          sx={{ mb: 2 }}
          value={form.name}
          onChange={(e) => setForm({ ...form, name: e.target.value })}
        />

        <TextField
          fullWidth
          label="Account Number"
          name="accountNumber"
          sx={{ mb: 2 }}
          value={form.accountNumber}
          onChange={(e) => setForm({ ...form, accountNumber: e.target.value })}
        />

        <Autocomplete
          fullWidth
          options={bankList}
          getOptionLabel={(option) => option.bank_name}
          sx={{ mb: 2 }}
          onChange={(e, selected) =>
            setForm({
              ...form,
              bankId: selected?.bankID || "",
              ifsc: selected?.ifsc_code || "",
            })
          }
          renderInput={(params) => <TextField {...params} label="Search Bank" />}
        />

        <TextField
          fullWidth
          label="IFSC Code"
          name="ifsc"
          sx={{ mb: 2 }}
          value={form.ifsc}
          disabled
        />

        <Button
          fullWidth
          variant="contained"
          sx={{
            borderRadius: "50px",
            backgroundColor: "var(--bs-primary2)",
            color: "var(--bs-primary1)",
            fontWeight: 700,
            mt: 1,
          }}
          onClick={handleSubmit}
        >
          Add Beneficiary
        </Button>

        <Button
          fullWidth
          sx={{
            borderRadius: "50px",
            mt: 1,
            color: "var(--bs-primary2)",
            fontWeight: 700,
          }}
          onClick={() => setCurrentPage("ppi-send-money")}
        >
          Cancel
        </Button>
      </Card>

      {/* OTP Dialog */}
      <Dialog open={openOtpPopup} onClose={() => setOpenOtpPopup(false)} fullWidth maxWidth="xs">
        <DialogContent
          sx={{
            p: 3,
            backgroundColor: "var(--bs-primary1)",
            borderRadius: "16px",
            border: "1px solid var(--bs-primary2)",
            color: "var(--bs-primary2)",
          }}
        >
          <Typography textAlign="center" fontWeight={700} mb={2}>
            Enter OTP
          </Typography>

          <TextField
            fullWidth
            label="OTP"
            value={otp}
            inputProps={{ maxLength: 6 }}
            onChange={(e) => setOtp(e.target.value.replace(/\D/g, ""))}
          />

          <Button
            fullWidth
            variant="contained"
            sx={{
              mt: 3,
              borderRadius: "50px",
              backgroundColor: "var(--bs-primary2)",
              color: "var(--bs-primary1)",
              fontWeight: 700,
            }}
            onClick={handleVerifyOtp}
          >
            Verify OTP
          </Button>
        </DialogContent>
      </Dialog>
    </PpiPageLayout>
  );
};

export default PpiAddBeneficiary;
