import React from "react";
import { Box, Typography } from "@mui/material";

const PpiPageLayout = ({ title, children }) => {
  return (
    <Box
      p={{ xs: 1, sm: 1, md: 2 }}
      sx={{
        width: "100%",
        maxWidth: "1400px",
        margin: "0 auto",
        mt: { xs: 1, md: 0 },
      }}
    >
      <Typography
        variant="h4"
        fontWeight={700}
        mb={3}
        textAlign="center"
        color="var(--bs-primary2)"
      >
        {title}
      </Typography>

      {children}
    </Box>
  );
};

export default PpiPageLayout;
