import React, { useState, useEffect } from "react";
import {
  Box,
  Card,
  Typography,
  Button,
  Dialog,
  DialogContent,
  TextField,
  MenuItem,
  Grid,
  Divider,
  useMediaQuery,
} from "@mui/material";
import { toast } from "react-toastify";
import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import {
  generateTransactionOtp,
  ppiTransaction,
  deletePpiBeneficiary,
  deletePpiBeneficiaryVerify,
} from "../../api/login/Login";
import PpiPageLayout from "./PpiPageLayout";

const PpiSendMoney = () => {
  const { beneficiaryList, setCurrentPage } = usePpiBankTransfer();
  const remitter = beneficiaryList;
  const beneficiaries = remitter?.beneficiaries || [];

  const [openPopup, setOpenPopup] = useState(false);
  const [openDeletePopup, setOpenDeletePopup] = useState(false);
  const [selectedBen, setSelectedBen] = useState(null);
  const [showOtpField, setShowOtpField] = useState(false);
  const isMobile = useMediaQuery("(max-width:600px)");

  const [location, setLocation] = useState({ latitude: "0", longitude: "0" });
  const [form, setForm] = useState({ transferMode: "", transferAmount: "", otp: "" });
  const [mobile, setMobile] = useState("");

  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  useEffect(() => {
    navigator.geolocation?.getCurrentPosition(
      (pos) =>
        setLocation({ latitude: pos.coords.latitude, longitude: pos.coords.longitude }),
      () => toast.warning("Location Disabled, Using Default")
    );
  }, []);

  const handleOpenPopup = (ben) => {
    setSelectedBen(ben);
    setOpenPopup(true);
    setShowOtpField(false);
    setForm({ transferMode: "", transferAmount: "", otp: "" });
  };

  const handleSendOtp = async () => {
    if (!form.transferMode || !form.transferAmount)
      return toast.error("Enter Transfer Mode & Amount");

    if (mobile.length !== 10) return toast.error("Invalid mobile number");

    try {
      const res = await generateTransactionOtp({
        accountNumber: selectedBen.account,
        ifsc: selectedBen.ifsc,
        beneficiaryId: selectedBen.id,
        transferMode: form.transferMode,
        amount: form.transferAmount,
        mobile,
        latitude: location.latitude,
        longitude: location.longitude,
      });

      const statusCode = res?.data?.data?.statuscode;

      if (statusCode === "TXN") {
        toast.success("OTP Sent Successfully");
        setShowOtpField(true);
      } else if (statusCode === "IST") {
        toast.error(res?.data?.data?.status || "Session expired. Please login again");
        localStorage.removeItem("mobile");
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(res?.data?.data?.status || "Failed to send OTP");
      }
    } catch (err) {
      toast.error("Something went wrong");
    }
  };

  const handleConfirmPayment = async () => {
    if (!form.otp) return toast.error("Enter OTP");

    try {
      const res = await ppiTransaction({
        accountNumber: selectedBen.account,
        ifsc: selectedBen.ifsc,
        transferMode: form.transferMode,
        amount: form.transferAmount,
        otp: form.otp,
        mobile,
        latitude: location.latitude,
        longitude: location.longitude,
      });

      const api = res?.data?.data;
      const statusCode = api?.statuscode;

      if (statusCode === "TXN") {
        toast.success(api?.status || "Transaction Success");
        
        setOpenPopup(false);
      }
      else if (statusCode === "IST") {
        toast.error(api?.status || "Invalid or expired session token");
        localStorage.removeItem("mobile");
        setOpenPopup(false);
        setCurrentPage("ppi-status-page");
      }
      else {
        toast.error(api?.status || "Transaction Failed");
      }

    } catch (error) {
      toast.error(error?.response?.data?.message || "Something went wrong");
    }
  };


  const handleDeleteBeneficiary = async (ben) => {
    setSelectedBen(ben);

    try {
      const res = await deletePpiBeneficiary({
        beneficiaryId: ben?.id,
        mobile,
      });

      const apiData = res?.data?.data;
      const statusCode = apiData?.statuscode;

      if (statusCode === "OTP") {
        toast.success("OTP Sent for Delete Verification");
        setOpenDeletePopup(true);
      }
      else if (statusCode === "ERR") {
        toast.error(apiData?.status || "Please wait before requesting new OTP");
      }
      else if (statusCode === "IST") {
        toast.error(apiData?.status || "Invalid or expired session token");
        localStorage.removeItem("mobile");
        setOpenDeletePopup(false);
        setCurrentPage("ppi-status-page");
      }
      else {
        toast.error(apiData?.status || "Failed to send delete OTP");
      }

    } catch (err) {
      toast.error(err?.response?.data?.message || "Delete request failed");
    }
  };


  const handleDeleteBeneficiaryVerify = async () => {
    try {
      const res = await deletePpiBeneficiaryVerify({
        beneficiaryId: selectedBen?.id,
        otp: form.otp,
        mobile,
      });

      const api = res?.data?.data;
      const statusCode = api?.statuscode;

      if (statusCode === "TXN") {
        toast.success("Beneficiary Removed Successfully");
        setOpenDeletePopup(false);
        setCurrentPage("ppi-status-page");
      }
      else if (statusCode === "IST") {
        toast.error(api?.status || "Invalid or expired session token");
        localStorage.removeItem("mobile");
        setOpenDeletePopup(false);
        setCurrentPage("ppi-status-page");
      }
      else {
        toast.error(api?.status || "Delete failed");
      }

    } catch (err) {
      toast.error(err?.response?.data?.message || "Invalid OTP");
    }
  };

  return (
    <PpiPageLayout title="Send Money">
      <Card
        sx={{
          p: { xs: 2, sm: 3 },
          mb: 3,
          width: "100%",
          borderRadius: "14px",
          background: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          border: "1.5px solid var(--bs-primary2)",
          boxShadow: "none",
        }}
      >
        <Typography fontWeight={700} fontSize={{ xs: 18, sm: 20 }}>
          Remitter Details
        </Typography>

        <Divider sx={{ my: 1, borderColor: "var(--bs-primary2)" }} />

        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Name:</b> {remitter?.firstName} {remitter?.lastName}
            </Typography>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Mobile:</b> {mobile}
            </Typography>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Total Limit:</b> ₹{remitter?.limitTotal}
            </Typography>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Available:</b> ₹{remitter?.limitAvailable}
            </Typography>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Used:</b> ₹{remitter?.limitConsumed}
            </Typography>
          </Grid>
        </Grid>
      </Card>
      <Box display="flex" justifyContent="flex-end" mb={2}>
        <Button
          variant="contained"
          sx={{
            borderRadius: "30px",
            fontWeight: 700,
            px: 4,
            py: 1.2,
            backgroundColor: "var(--bs-primary2)",
            color: "var(--bs-primary1)",
          }}
          onClick={() => setCurrentPage("ppi-add-beneficiary")}
        >
          + Add Beneficiary
        </Button>
      </Box>


      <Grid container spacing={2}>
        {beneficiaries
          ?.sort((a, b) => {
            if (a.bankStatus === "REGISTERED" && b.bankStatus === "UNREGISTERED") return -1;
            if (a.bankStatus === "UNREGISTERED" && b.bankStatus === "REGISTERED") return 1;
            return 0;
          })
          .map((ben) => (
            <Grid key={ben.id} item xs={12} sm={6} md={4} lg={3} xl={2}>

              <Card
                sx={{
                  p: 3,
                  borderRadius: "18px",
                  backgroundColor:
                    ben.bankStatus === "UNREGISTERED"
                      ? "rgba(200,200,200,0.3)"
                      : "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                  position: "relative",
                  boxShadow: "0 8px 16px rgba(0,0,0,0.3)",
                  transition: "0.3s",
                  "&:hover":
                    ben.bankStatus === "REGISTERED"
                      ? { transform: "translateY(-8px)" }
                      : {},
                }}
              >
                {ben.bankStatus === "UNREGISTERED" && (
                  <Typography
                    sx={{
                      position: "absolute",
                      top: 10,
                      right: 10,
                      background: "red",
                      color: "white",
                      fontSize: "11px",
                      fontWeight: 700,
                      px: 1,
                      borderRadius: "5px",
                    }}
                  >
                    Deleted
                  </Typography>
                )}

                <Typography fontWeight={700} fontSize={{ xs: 17, sm: 18 }}>
                  {ben.name}
                </Typography>
                <Typography fontSize={15}>Account: {ben.account}</Typography>
                <Typography fontSize={15}>IFSC: {ben.ifsc}</Typography>
                <Typography fontSize={15}>Bank: {ben.bank}</Typography>

                <Box display="flex" justifyContent="space-between" gap={1} mt={2}>
                  {/* SEND MONEY BUTTON */}
                  <Button
                    fullWidth
                    variant="contained"
                    disabled={ben.bankStatus === "UNREGISTERED"}
                    sx={{
                      borderRadius: "30px",
                      backgroundColor:
                        ben.bankStatus === "UNREGISTERED"
                          ? "#b0b0b0"
                          : "var(--bs-primary2)",
                      color:
                        ben.bankStatus === "UNREGISTERED"
                          ? "#fff"
                          : "var(--bs-primary1)",
                    }}
                    onClick={() => ben.bankStatus === "REGISTERED" && handleOpenPopup(ben)}
                  >
                    Send Money
                  </Button>

                  {/* DELETE BUTTON */}
                  <Button
                    fullWidth
                    variant="outlined"
                    disabled={ben.bankStatus === "UNREGISTERED"}
                    sx={{
                      borderRadius: "30px",
                      fontWeight: 700,
                      borderColor:
                        ben.bankStatus === "UNREGISTERED" ? "#b0b0b0" : "red",
                      color:
                        ben.bankStatus === "UNREGISTERED" ? "#b0b0b0" : "red",
                    }}
                    onClick={() =>
                      ben.bankStatus === "REGISTERED" && handleDeleteBeneficiary(ben)
                    }
                  >
                    Delete
                  </Button>
                </Box>
              </Card>
            </Grid>
          ))}
      </Grid>





      {/* SEND MONEY POPUP */}
      <Dialog open={openPopup} onClose={() => setOpenPopup(false)} fullWidth maxWidth="xs" fullScreen={isMobile}>
        <DialogContent
          sx={{
            p: { xs: 2, sm: 4 },
            backgroundColor: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            borderRadius: "20px",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography fontWeight={700} textAlign="center" fontSize={{ xs: 18, sm: 20 }}>
            Send Money
          </Typography>

          <Typography fontWeight={500} mb={2} fontSize="14px" mt={2}>
            Verify your identity to continue
          </Typography>

          <TextField fullWidth label="Mobile" value={mobile} disabled sx={{ mb: 2 }} />
          <TextField fullWidth label="Account" value={selectedBen?.account} disabled sx={{ mt: 2 }} />
          <TextField fullWidth label="IFSC" value={selectedBen?.ifsc} disabled sx={{ mt: 2 }} />

          <TextField select fullWidth sx={{ mt: 2 }} label="Transfer Mode"
            onChange={(e) => setForm({ ...form, transferMode: e.target.value })}
          >
            <MenuItem value="IMPS">IMPS</MenuItem>
            <MenuItem value="NEFT">NEFT</MenuItem>
          </TextField>

          <TextField fullWidth label="Amount" sx={{ mt: 2 }}
            onChange={(e) => setForm({ ...form, transferAmount: e.target.value })}
          />

          {showOtpField && (
            <TextField fullWidth label="Enter OTP" sx={{ mt: 2 }}
              onChange={(e) => setForm({ ...form, otp: e.target.value })}
            />
          )}

          {!showOtpField ? (
            <Button fullWidth variant="contained" sx={{ mt: 3, borderRadius: "30px", backgroundColor: "var(--bs-primary2)" }}
              onClick={handleSendOtp}
            >
              Send OTP
            </Button>
          ) : (
            <Button fullWidth variant="contained" sx={{ mt: 3, borderRadius: "30px", backgroundColor: "var(--bs-primary2)" }}
              onClick={handleConfirmPayment}
            >
              Confirm Payment
            </Button>
          )}

          <Button fullWidth sx={{ mt: 2, borderRadius: "30px", color: "var(--bs-primary2)" }}
            onClick={() => setOpenPopup(false)}
          >
            Cancel
          </Button>
        </DialogContent>
      </Dialog>

      {/* DELETE OTP POPUP */}
      <Dialog
        open={openDeletePopup}
        onClose={() => setOpenDeletePopup(false)}
        fullWidth
        maxWidth="xs"
      >
        <DialogContent
          sx={{
            p: 3,
            backgroundColor: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            borderRadius: "20px",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography
            fontWeight={700}
            textAlign="center"
            fontSize={{ xs: 18, sm: 20 }}
            mb={2}
          >
            Enter OTP
          </Typography>

          <TextField
            fullWidth
            label="OTP"
            value={form.otp}
            inputProps={{ maxLength: 6 }}
            onChange={(e) =>
              setForm({ ...form, otp: e.target.value.replace(/\D/g, "") })
            }
          />

          <Button
            fullWidth
            variant="contained"
            sx={{
              mt: 3,
              backgroundColor: "red",
              borderRadius: "50px",
              color: "white",
              fontWeight: "bold",
            }}
            onClick={handleDeleteBeneficiaryVerify}
          >
            Confirm Delete
          </Button>

          <Button
            fullWidth
            sx={{
              mt: 2,
              borderRadius: "50px",
              color: "var(--bs-primary2)",
              fontWeight: "bold",
            }}
            onClick={() => setOpenDeletePopup(false)}
          >
            Cancel
          </Button>
        </DialogContent>
      </Dialog>


    </PpiPageLayout>
  );
};

export default PpiSendMoney;
